import React from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../../hooks/Auth/useAuth';
import Navbar from '../Navbar/Navbar';
import './Demo.css';

const Demo = () => {
  const { userData } = useAuth();
  
  const exemploLink = userData?.linkCalendario 
    ? `${window.location.origin}/calendario/${userData.linkCalendario}`
    : `${window.location.origin}/calendario/exemplo12345`;

  return (
    <div>
      <Navbar />
      <div className="demo-container">
      <div className="demo-card">
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px' }}>
          <h2> Como Funciona o Sistema de Calendrios</h2>
          <Link to="/" style={{ textDecoration: 'none' }}>
            <button style={{
              padding: '8px 16px',
              backgroundColor: '#6c757d',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '14px'
            }}>
               Voltar ao Calendrio
            </button>
          </Link>
        </div>
        
        <div className="demo-section">
          <h3> Para Administradores (Voc)</h3>
          <ul>
            <li>Crie sua conta como administrador de uma instituio</li>
            <li>Adicione eventos ao seu calendrio</li>
            <li>Escolha se quer tornar o calendrio pblico ou privado</li>
            <li>Se pblico, compartilhe o link com outras pessoas</li>
          </ul>
        </div>

        <div className="demo-section">
          <h3> Para Visitantes (Sem Conta)</h3>
          <ul>
            <li>No precisam criar conta</li>
            <li>Podem visualizar calendrios pblicos atravs do link</li>
            <li>Veem os eventos de forma somente leitura</li>
            <li>No podem criar ou editar eventos</li>
          </ul>
        </div>

        <div className="demo-section">
          <h3> Exemplo de Link Pblico</h3>
          <div className="link-demo">
            <code>{exemploLink}</code>
          </div>
          <p className="demo-note">
             Este  um exemplo de como seria o link do seu calendrio pblico. 
            Qualquer pessoa pode acessar este link e ver os eventos, mesmo sem ter conta no sistema.
          </p>
        </div>

        <div className="demo-section">
          <h3> Privacidade e Controle</h3>
          <ul>
            <li><strong>Privado:</strong> S voc v os eventos (padro)</li>
            <li><strong>Pblico:</strong> Qualquer pessoa com o link pode ver</li>
            <li>Voc pode alternar entre pblico/privado a qualquer momento</li>
            <li>O link  nico e no pode ser adivinhado</li>
          </ul>
        </div>

        <div className="demo-actions">
          <button 
            onClick={() => window.open(exemploLink, '_blank')}
            className="demo-button"
          >
             Testar Link Pblico
          </button>
          <button 
            onClick={() => window.location.href = '/configuracoes'}
            className="demo-button secondary"
          >
             Configurar Visibilidade
          </button>
        </div>
      </div>
    </div>
    </div>
  );
};

export default Demo;
